import { NextRequest, NextResponse } from 'next/server';
import fs from 'fs';
import path from 'path';

export async function POST(request: NextRequest) {
  try {
    const data = await request.json();
    const { 
      slug, 
      title, 
      description, 
      date, 
      icon, 
      content, 
      colors, 
      song,
      songTitle,
      songArtist,
      songCover 
    } = data;
    
    // Validate required fields
    if (!slug || !title || !description || !date || !icon || !content || !colors) {
      return NextResponse.json(
        { error: 'Missing required fields' },
        { status: 400 }
      );
    }
    
    // Check for valid slug format (lowercase, hyphens)
    if (!/^[a-z0-9-]+$/.test(slug)) {
      return NextResponse.json(
        { error: 'Invalid slug format. Use lowercase letters, numbers, and hyphens only.' },
        { status: 400 }
      );
    }
    
    // Path to the blog post directory
    const blogDirectory = path.join(process.cwd(), 'app/blog', slug);
    
    // Check if directory already exists
    if (fs.existsSync(blogDirectory)) {
      return NextResponse.json(
        { error: 'Blog post with this slug already exists' },
        { status: 409 }
      );
    }
    
    // Create directory
    fs.mkdirSync(blogDirectory, { recursive: true });
    
    // Format the date
    const formattedDate = new Date(date).toLocaleDateString('en-US', {
      month: 'long',
      day: 'numeric',
      year: 'numeric'
    });
    
    // Create the gradient configurations
    const gradients = [
      `from-${colors[0]}-400 via-${colors[1]}-300 to-${colors[2]}-300`,
      `from-${colors[2]}-300 via-${colors[3]}-400 to-${colors[4]}-300`,
      `from-${colors[4]}-300 via-${colors[5]}-300 to-${colors[0]}-400`
    ];
    
    // Create the blog post template with proper template literals
    const blogPostTemplate = `'use client';

import { motion } from 'framer-motion';
import Link from 'next/link';
import { useRef, useEffect, useState } from 'react';
import ImageIcon from '../../components/ImageIcon';

export default function ${title.replace(/\s+/g, '')}Post() {
  const [scrollY, setScrollY] = useState(0);
  const contentRef = useRef<HTMLDivElement>(null);
  const [progress, setProgress] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isLooping, setIsLooping] = useState(false);
  const audioRef = useRef<HTMLAudioElement>(null);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const progressBarRef = useRef<HTMLDivElement>(null);
  
  useEffect(() => {
    const handleScroll = () => {
      setScrollY(window.scrollY);
      
      if (contentRef.current) {
        const { height } = contentRef.current.getBoundingClientRect();
        const scrollableHeight = height - window.innerHeight;
        setProgress(Math.min(1, window.scrollY / scrollableHeight));
      }
    };
    
    window.addEventListener('scroll', handleScroll, { passive: true });
    
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  
  // Audio player controls
  const togglePlay = () => {
    if (!audioRef.current) return;
    
    if (isPlaying) {
      audioRef.current.pause();
    } else {
      audioRef.current.play();
    }
    
    setIsPlaying(!isPlaying);
  };
  
  // Toggle loop
  const toggleLoop = () => {
    if (!audioRef.current) return;
    
    audioRef.current.loop = !isLooping;
    setIsLooping(!isLooping);
  };
  
  // Scrubbing functionality
  const handleProgressBarClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!progressBarRef.current || !audioRef.current || !duration) return;
    
    const rect = progressBarRef.current.getBoundingClientRect();
    const pos = (e.clientX - rect.left) / rect.width;
    const newTime = pos * duration;
    
    audioRef.current.currentTime = newTime;
    setCurrentTime(newTime);
  };
  
  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;
    
    const updateTime = () => setCurrentTime(audio.currentTime);
    const updateDuration = () => setDuration(audio.duration);
    const handleEnded = () => setIsPlaying(false);
    
    audio.addEventListener('timeupdate', updateTime);
    audio.addEventListener('durationchange', updateDuration);
    audio.addEventListener('ended', handleEnded);
    
    return () => {
      audio.removeEventListener('timeupdate', updateTime);
      audio.removeEventListener('durationchange', updateDuration);
      audio.removeEventListener('ended', handleEnded);
    };
  }, []);
  
  // Format time in MM:SS
  const formatTime = (time: number) => {
    if (!time || isNaN(time)) return "0:00";
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return \`\${minutes}:\${seconds < 10 ? '0' : ''}\${seconds}\`;
  };
  
  // Calculate progress percentage
  const progressPercentage = duration ? (currentTime / duration) * 100 : 0;
  
  // Generate grainy animated gradient
  const GrainyGradient = ({ colorClass }: { colorClass: string }) => {
    return (
      <div className="relative w-full h-full overflow-hidden rounded-lg">
        <div className={\`absolute inset-0 \${colorClass} animate-gradient-x\`}></div>
        <div className="absolute inset-0 opacity-40" style={{ 
          backgroundImage: 'url("data:image/svg+xml,%3Csvg viewBox=\\'0 0 250 250\\' xmlns=\\'http://www.w3.org/2000/svg\\'%3E%3Cfilter id=\\'noiseFilter\\'%3E%3CfeTurbulence type=\\'fractalNoise\\' baseFrequency=\\'0.65\\' numOctaves=\\'3\\' stitchTiles=\\'stitch\\'/%3E%3C/filter%3E%3Crect width=\\'100%25\\' height=\\'100%25\\' filter=\\'url(%23noiseFilter)\\'/%3E%3C/svg%3E")',
          filter: 'contrast(150%) brightness(1000%)',
          mixBlendMode: 'multiply',
        }}></div>
      </div>
    );
  };
  
  // Text animation variants
  const textVariants = {
    hidden: { opacity: 0, y: 10 },
    visible: (i: number) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: 0.05 * i,
        duration: 0.5,
        ease: [0.215, 0.61, 0.355, 1]
      }
    })
  };
  
  return (
    <div className="relative min-h-screen bg-white dark:bg-gray-900" ref={contentRef}>
      {/* Hidden audio element */}
      <audio ref={audioRef} src="${song}" preload="metadata" loop={isLooping} />
      
      {/* Reading progress indicator */}
      <motion.div 
        className="fixed top-0 left-0 right-0 h-0.5 bg-gradient-to-r from-${colors[0]}-500 via-${colors[3]}-500 to-${colors[5]}-500 z-50"
        style={{ scaleX: progress, transformOrigin: 'left' }}
      />
      
      <motion.div
        className="mx-auto max-w-[55%] px-8 pt-24 pb-32"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
      >
        {/* Back link with animation */}
        <motion.div
          className="mb-12"
          initial={{ opacity: 0, x: -10 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5 }}
        >
          <Link 
            href="/blog" 
            className="inline-flex items-center text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300 transition-colors"
          >
            <svg 
              xmlns="http://www.w3.org/2000/svg" 
              className="h-4 w-4 mr-2" 
              fill="none" 
              viewBox="0 0 24 24" 
              stroke="currentColor"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
            back to blog
          </Link>
        </motion.div>
        
        {/* Header with square gradient icon */}
        <div className="flex items-start mb-8">
          <div className="mr-5 flex-shrink-0">
            <ImageIcon 
              src="${icon}"
              size="lg"
              borderGradient="from-${colors[0]}-200 via-${colors[2]}-300 to-${colors[4]}-200"
              withHoverEffect={true}
              alt="${title} post icon"
            />
          </div>
          
          <div>
            <motion.h1 
              className="text-3xl font-semibold mb-2 text-black dark:text-white"
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.1 }}
            >
              ${title.toLowerCase()}
            </motion.h1>
            
            <motion.h2
              className="text-xl mb-3 text-gray-600 dark:text-gray-400 font-light"
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              ${description.toLowerCase()}
            </motion.h2>
            
            <motion.div
              className="text-sm text-gray-500 dark:text-gray-500"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ duration: 0.5, delay: 0.3 }}
            >
              ${formattedDate}
            </motion.div>
          </div>
        </div>
        
        {/* Custom Player Widget */}
        <motion.div
          className="w-full mb-10 overflow-hidden rounded-lg shadow-md"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.4 }}
          whileHover={{ scale: 1.01 }}
        >
          <div className="relative p-4 rounded-lg bg-gradient-to-r from-gray-100 to-gray-50 dark:from-gray-800 dark:to-gray-900">
            <div className="flex items-center relative z-10">
              <div className="flex-shrink-0 mr-4 relative">
                {/* Album art with subtle breathing animation when playing */}
                <motion.div 
                  className="w-12 h-12 rounded-md overflow-hidden shadow-md relative"
                  animate={isPlaying ? 
                    { scale: [1, 1.02, 1], boxShadow: ["0px 2px 8px rgba(0,0,0,0.1)", "0px 4px 12px rgba(0,0,0,0.2)", "0px 2px 8px rgba(0,0,0,0.1)"] } 
                    : {}}
                  transition={{ 
                    duration: 2,
                    repeat: isPlaying ? Infinity : 0,
                    ease: "easeInOut"
                  }}
                >
                  <img 
                    src={\`\${songCover || '/default-cover.jpg'}\`} 
                    alt={\`\${songTitle || 'Music'} - \${songArtist || 'Artist'}\`} 
                    className="w-full h-full object-cover"
                  />
                  
                  {/* Overlay for playing indicator */}
                  {isPlaying && (
                    <div className="absolute bottom-1 right-1">
                      <div className="flex space-x-0.5">
                        <motion.div 
                          className="w-1 h-2 bg-white rounded-full" 
                          animate={{ height: [2, 5, 2] }}
                          transition={{ duration: 0.8, repeat: Infinity, ease: "easeInOut" }}
                        />
                        <motion.div 
                          className="w-1 h-2 bg-white rounded-full" 
                          animate={{ height: [4, 2, 4] }}
                          transition={{ duration: 0.8, repeat: Infinity, ease: "easeInOut", delay: 0.2 }}
                        />
                        <motion.div 
                          className="w-1 h-2 bg-white rounded-full" 
                          animate={{ height: [3, 6, 3] }}
                          transition={{ duration: 0.8, repeat: Infinity, ease: "easeInOut", delay: 0.4 }}
                        />
                      </div>
                    </div>
                  )}
                </motion.div>
              </div>
              
              <div className="flex-grow">
                <div className="mb-1">
                  <span className="text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                    now playing
                  </span>
                </div>
                <div className="font-medium text-gray-900 dark:text-white text-sm">
                  ${songTitle ? songTitle.toLowerCase() : 'untitled'}
                </div>
                <div className="text-xs text-gray-500 dark:text-gray-400">
                  ${songArtist ? songArtist.toLowerCase() : 'unknown artist'}
                </div>
              </div>
              
              <div className="flex">
                <button 
                  onClick={togglePlay}
                  className="w-8 h-8 flex items-center justify-center rounded-full bg-${colors[0]}-100 dark:bg-${colors[0]}-900/30 text-${colors[0]}-700 dark:text-${colors[0]}-400 hover:bg-${colors[0]}-200 dark:hover:bg-${colors[0]}-800/40 transition-colors"
                >
                  {isPlaying ? (
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zM7 8a1 1 0 012 0v4a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v4a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                    </svg>
                  ) : (
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM9.555 7.168A1 1 0 008 8v4a1 1 0 001.555.832l3-2a1 1 0 000-1.664l-3-2z" clipRule="evenodd" />
                    </svg>
                  )}
                </button>
                
                <button 
                  onClick={toggleLoop}
                  className={\`ml-2 w-8 h-8 flex items-center justify-center rounded-full \${
                    isLooping 
                      ? 'bg-${colors[3]}-100 dark:bg-${colors[3]}-900/30 text-${colors[3]}-700 dark:text-${colors[3]}-400 hover:bg-${colors[3]}-200 dark:hover:bg-${colors[3]}-800/40' 
                      : 'bg-gray-100 dark:bg-gray-800/30 text-gray-500 dark:text-gray-400 hover:bg-gray-200 dark:hover:bg-gray-700/40'
                  } transition-colors\`}
                  title={isLooping ? "Disable loop" : "Enable loop"}
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                    <path d="M10 3a7 7 0 00-7 7v1a1 1 0 001 1h4a1 1 0 001-1v-1a1 1 0 00-1-1H6.414l1.293-1.293a5 5 0 017.07-7.07l-1.06 1.06a3 3 0 00-4.242 0L8.172 4H9a1 1 0 011 1v1a1 1 0 01-1 1H5a1 1 0 01-1-1V4a1 1 0 011-1h1.657l1.414-1.414A7 7 0 0110 3zm7 11v-1a1 1 0 00-1-1h-4a1 1 0 00-1 1v1a1 1 0 001 1h1.586l-1.293 1.293a5 5 0 01-7.07 7.07l1.06-1.06a3 3 0 004.242 0L11.828 16H11a1 1 0 01-1-1v-1a1 1 0 011-1h4a1 1 0 011 1v2a1 1 0 01-1 1h-1.657l-1.414 1.414A7 7 0 0110 17a7 7 0 007-7z" />
                  </svg>
                </button>
              </div>
            </div>
            
            {/* Progress bar */}
            <div className="mt-4 mx-1" ref={progressBarRef} onClick={handleProgressBarClick}>
              <div className="h-1 w-full bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden cursor-pointer">
                <div 
                  className="h-full bg-gradient-to-r from-${colors[1]}-300 to-${colors[3]}-300 rounded-full" 
                  style={{ width: \`\${progressPercentage}%\` }}
                />
              </div>
              <div className="flex justify-between mt-1 text-xs text-gray-500 dark:text-gray-400">
                <span>{formatTime(currentTime)}</span>
                <span>{formatTime(duration)}</span>
              </div>
            </div>
          </div>
        </motion.div>
        
        {/* First grainy gradient */}
        <motion.div
          className="w-full h-40 rounded-lg overflow-hidden mb-12"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.6 }}
        >
          <GrainyGradient colorClass="bg-gradient-to-r ${gradients[0]}" />
        </motion.div>
        
        {/* Article content with cleaner typography */}
        <motion.div className="space-y-6 text-gray-800 dark:text-gray-200">
          {content.split('\\n\\n').map((paragraph: string, index: number) => (
            <motion.p 
              key={index}
              className="font-light text-lg leading-relaxed"
              custom={index + 1}
              initial="hidden"
              animate="visible"
              variants={textVariants}
            >
              {paragraph.toLowerCase()}
            </motion.p>
          ))}
          
          {/* Second grainy gradient */}
          <motion.div
            className="w-full h-40 rounded-lg overflow-hidden my-12"
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.8 }}
          >
            <GrainyGradient colorClass="bg-gradient-to-r ${gradients[1]}" />
          </motion.div>
          
          {/* Third grainy gradient */}
          <motion.div
            className="w-full h-40 rounded-lg overflow-hidden my-12"
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.8 }}
          >
            <GrainyGradient colorClass="bg-gradient-to-r ${gradients[2]}" />
          </motion.div>
        </motion.div>
      </motion.div>
    </div>
  );
}`;
    
    // Write the blog post file
    fs.writeFileSync(path.join(blogDirectory, 'page.tsx'), blogPostTemplate);
    
    // Update blog index page
    const updateBlogIndex = () => {
      try {
        const blogListPath = path.join(process.cwd(), 'app/api/blog/list/route.ts');
        // Create the blog list API if it doesn't exist
        if (!fs.existsSync(blogListPath)) {
          const blogListApiTemplate = `import { NextResponse } from 'next/server';
import fs from 'fs';
import path from 'path';

export async function GET() {
  const blogDir = path.join(process.cwd(), 'app/blog');
  
  try {
    const entries = fs.readdirSync(blogDir, { withFileTypes: true });
    const directories = entries
      .filter(entry => entry.isDirectory() && entry.name !== 'components' && !entry.name.startsWith('.'))
      .map(dir => dir.name);
    
    return NextResponse.json({ blogPosts: directories });
  } catch (error) {
    console.error('Error reading blog posts:', error);
    return NextResponse.json(
      { error: 'Failed to read blog posts' },
      { status: 500 }
    );
  }
}`;
          fs.writeFileSync(blogListPath, blogListApiTemplate);
        }
      } catch (error) {
        console.error('Error updating blog index:', error);
      }
    };
    
    updateBlogIndex();
    
    return NextResponse.json({ success: true, slug });
  } catch (error) {
    console.error('Error creating blog post:', error);
    return NextResponse.json(
      { error: 'Failed to create blog post' },
      { status: 500 }
    );
  }
} 